@use 'base/variables' as v;
@use 'base/mixins' as m;


.tecnologias {
    background: v.$azulOscuro;
    background-image: url(../img/pattern.png);
    padding: 5rem 0;

    h3, p{
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
    }

    .nivel {
        margin-bottom: 3rem;        
    }
}

.tecnologia-contenedor{
    @include m.tablet {
        @include m.grid(2, 5rem)
    }

    
    p {
        color: v.$blanco;
        font-weight: 700;
    }    

    .text-tecnologia {
        font-weight: 400;
        text-transform: none;
        padding: 0 4rem;
    }
}

.card-tecnologia {
    margin-bottom: 5rem;
    border-radius: 1rem;

    .nombre-tecnologia {
        &::before{
            content: '';
            display: block;
            width: 20rem;
            height: 20rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 0 auto;
        }
    }
}

.html {
    -webkit-box-shadow: 0px 10px 10px 3px rgba(255,128,0,1);
    -moz-box-shadow: 0px 10px 10px 3px rgba(255,128,0,1);
    box-shadow: 0px 10px 10px 3px rgba(255,128,0,1);
}

.css{ 
    -webkit-box-shadow: 0px 10px 10px 3px rgba(21,114,182,1);
-moz-box-shadow: 0px 10px 10px 3px rgba(21,114,182,1);
box-shadow: 0px 10px 10px 3px rgba(21,114,182,1);
}

.js{
    -webkit-box-shadow: 0px 10px 10px 3px rgba(247,223,30,1);
-moz-box-shadow: 0px 10px 10px 3px rgba(247,223,30,1);
box-shadow: 0px 10px 10px 3px rgba(247,223,30,1);
}

.react {
    -webkit-box-shadow: 0px 10px 10px 3px rgba(0,216,255,1);
-moz-box-shadow: 0px 10px 10px 3px rgba(0,216,255,1);
box-shadow: 0px 10px 10px 3px rgba(0,216,255,1);
}


.html .nombre-tecnologia:before{background-image: url(../img/logoHTML.png);}
.css .nombre-tecnologia:before{background-image: url(../img/logoCSS.png);}
.js .nombre-tecnologia:before{background-image: url(../img/logoJS.png);}
.react .nombre-tecnologia:before{background-image: url(../img/logoREACT.png);}

.bg-tecnologia {
    background: radial-gradient(circle, rgba(26,34,50,1) 0%, rgba(0,0,0,1) 100%);
}


