@use 'base/variables' as v;
@use 'base/mixins' as m;

.info {

    background: v.$azulOscuro;
    background-image: url(../img/pattern.png);
    padding: 5rem 0;

    border-bottom: .3rem solid;
    border-image: linear-gradient(to right, rgba(0,232,54,1) 0%, rgba(142,255,0,1) 50%, rgba(212,255,17,1)) 1;

    h4,
    h3 {
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
    }

    h4 {
        font-size: 2rem;
        padding-top: 2rem;
    }

}

.contenido-info {
    margin-top: 2rem;

    @include m.tablet {
        @include m.grid(2, 3rem)
    }


}

.text-info {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    color: v.$blanco;
}

.lista {
    @include m.resetear-lista;
    color: v.$blanco;
    li {
        padding: 2rem;
        
        span {
            font-weight: 700;
        }
    }
    li:nth-child(odd){
        background-color: lighten(v.$negro, 2%);
    }

    li:hover{
        background-color: v.$verdeAmarillo;
        color: v.$azulOscuro;
    }
}

.bg-info{
    background: radial-gradient(circle, rgba(26,34,50,1) 0%, rgba(0,0,0,1) 100%);
    -webkit-box-shadow: 0px 3px 10px 2px rgba(142,255,0,1);
-moz-box-shadow: 0px 3px 10px 2px rgba(142,255,0,1);
box-shadow: 0px 3px 10px 2px rgba(142,255,0,1);
}
