@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        background: rgb(78,177,186);
        background: linear-gradient(270deg, rgba(78,177,186,0.5) 0%, rgba(26,34,50,0.5) 100%);


        display: flex;
        flex-direction: column;
        justify-content: center;
        
    }

    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }

    border-bottom: .3rem solid;
    border-image: linear-gradient(to right, rgba(0,232,54,1) 0%, rgba(142,255,0,1) 50%, rgba(212,255,17,1)) 1;
}

.contenido-video {
    z-index: 10;
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }

    p {
        background: rgb(212,255,17);
        background: linear-gradient(270deg, rgba(212,255,17,1) 0%, rgba(0,232,54,1) 100%);
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$azulOscuro;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}