@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-codeCreator {
    padding: 5rem 0;
    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.contenido-codeCreator {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }
    h2 {
        margin: 0;
        color: v.$azulOscuro;
    }

    .fecha {
        color: v.$verdeOscuro;
        font-weight: 700;
        margin: 0;
    }
}