@use 'base/variables' as v;
@use 'base/mixins' as m;


.header {
    background-color: v.$azulOscuro;
    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem v.$negro;
        }
    }

    h1 {
        text-align: center;
        background: linear-gradient(to right, v.$verdeAmarillo, v.$verdeOscuro);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-transform: uppercase;
        letter-spacing: 0.5rem;
        font-size: 5rem;
        @include m.tablet {
            margin: 0;
        }
    }

    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    
}

.nav-principal {
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    a {
        display: block;
        text-align: center;
        color: v.$verdeClaro;
        font-size: 2.4rem;
        font-weight: 600;
        margin-bottom: 1rem;

        @include m.tablet {
            margin-bottom: 0;
        }

        &:hover{
            color: v.$verdeAmarillo;
        }
    }
}