@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 5rem 0;

    h3 {
        text-align: center;
        color: v.$azulOscuro;
        text-transform: uppercase;
    }

    
}

.galerias  {
    border-top: .3rem solid;
    border-image: linear-gradient(to right, rgba(0,232,54,1) 0%, rgba(142,255,0,1) 50%, rgba(212,255,17,1)) 1;
    img{
        -webkit-box-shadow: 0px 3px 10px 2px rgba(26,34,50,1);
        -moz-box-shadow: 0px 3px 10px 2px rgba(26,34,50,1);
        box-shadow: 0px 3px 10px 2px rgba(26,34,50,1);
    }    
}

.galeria-img {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.phone {
        @include m.grid(3, 2rem);
    }
    @include m.tablet {
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}

.galeria-img picture img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;
    background-color: rgb(0 0 0 / .5);

    picture {
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background: linear-gradient(to right, v.$verdeAmarillo, v.$verdeOscuro);
    color: v.$azulOscuro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover{
        cursor: pointer;
    }
    
}