html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 9.75rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #1A2232;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #000;
  }
}
.header h1 {
  text-align: center;
  background: linear-gradient(to right, #D4FF11, #00E836);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav-principal {
    display: flex;
    gap: 2rem;
  }
}
.nav-principal a {
  display: block;
  text-align: center;
  color: #8EFF00;
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #D4FF11;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
  border-bottom: 0.3rem solid;
  border-image: linear-gradient(to right, rgb(0, 232, 54) 0%, rgb(142, 255, 0) 50%, rgb(212, 255, 17)) 1;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(78, 177, 186);
  background: linear-gradient(270deg, rgba(78, 177, 186, 0.5) 0%, rgba(26, 34, 50, 0.5) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1024px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background: rgb(212, 255, 17);
  background: linear-gradient(270deg, rgb(212, 255, 17) 0%, rgb(0, 232, 54) 100%);
  display: inline-block;
  padding: 1rem 2rem;
  color: #1A2232;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-codeCreator {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-codeCreator {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-codeCreator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-codeCreator {
    margin-top: 0;
  }
}
.contenido-codeCreator h2 {
  margin: 0;
  color: #1A2232;
}
.contenido-codeCreator .fecha {
  color: #00E836;
  font-weight: 700;
  margin: 0;
}

.tecnologias {
  background: #1A2232;
  background-image: url(../img/pattern.png);
  padding: 5rem 0;
}
.tecnologias h3, .tecnologias p {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
}
.tecnologias .nivel {
  margin-bottom: 3rem;
}

@media (min-width: 768px) {
  .tecnologia-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.tecnologia-contenedor p {
  color: #fff;
  font-weight: 700;
}
.tecnologia-contenedor .text-tecnologia {
  font-weight: 400;
  text-transform: none;
  padding: 0 4rem;
}

.card-tecnologia {
  margin-bottom: 5rem;
  border-radius: 1rem;
}
.card-tecnologia .nombre-tecnologia::before {
  content: "";
  display: block;
  width: 20rem;
  height: 20rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.html {
  -webkit-box-shadow: 0px 10px 10px 3px rgb(255, 128, 0);
  -moz-box-shadow: 0px 10px 10px 3px rgb(255, 128, 0);
  box-shadow: 0px 10px 10px 3px rgb(255, 128, 0);
}

.css {
  -webkit-box-shadow: 0px 10px 10px 3px rgb(21, 114, 182);
  -moz-box-shadow: 0px 10px 10px 3px rgb(21, 114, 182);
  box-shadow: 0px 10px 10px 3px rgb(21, 114, 182);
}

.js {
  -webkit-box-shadow: 0px 10px 10px 3px rgb(247, 223, 30);
  -moz-box-shadow: 0px 10px 10px 3px rgb(247, 223, 30);
  box-shadow: 0px 10px 10px 3px rgb(247, 223, 30);
}

.react {
  -webkit-box-shadow: 0px 10px 10px 3px rgb(0, 216, 255);
  -moz-box-shadow: 0px 10px 10px 3px rgb(0, 216, 255);
  box-shadow: 0px 10px 10px 3px rgb(0, 216, 255);
}

.html .nombre-tecnologia:before {
  background-image: url(../img/logoHTML.png);
}

.css .nombre-tecnologia:before {
  background-image: url(../img/logoCSS.png);
}

.js .nombre-tecnologia:before {
  background-image: url(../img/logoJS.png);
}

.react .nombre-tecnologia:before {
  background-image: url(../img/logoREACT.png);
}

.bg-tecnologia {
  background: radial-gradient(circle, rgb(26, 34, 50) 0%, rgb(0, 0, 0) 100%);
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  color: #1A2232;
  text-transform: uppercase;
}

.galerias {
  border-top: 0.3rem solid;
  border-image: linear-gradient(to right, rgb(0, 232, 54) 0%, rgb(142, 255, 0) 50%, rgb(212, 255, 17)) 1;
}
.galerias img {
  -webkit-box-shadow: 0px 3px 10px 2px rgb(26, 34, 50);
  -moz-box-shadow: 0px 3px 10px 2px rgb(26, 34, 50);
  box-shadow: 0px 3px 10px 2px rgb(26, 34, 50);
}

.galeria-img {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-img {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .galeria-img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.galeria-img picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background: linear-gradient(to right, #D4FF11, #00E836);
  color: #1A2232;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.info {
  background: #1A2232;
  background-image: url(../img/pattern.png);
  padding: 5rem 0;
  border-bottom: 0.3rem solid;
  border-image: linear-gradient(to right, rgb(0, 232, 54) 0%, rgb(142, 255, 0) 50%, rgb(212, 255, 17)) 1;
}
.info h4,
.info h3 {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
}
.info h4 {
  font-size: 2rem;
  padding-top: 2rem;
}

.contenido-info {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .contenido-info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.text-info {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  color: #fff;
}

.lista {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #fff;
}
.lista li {
  padding: 2rem;
}
.lista li span {
  font-weight: 700;
}
.lista li:nth-child(odd) {
  background-color: #050505;
}
.lista li:hover {
  background-color: #D4FF11;
  color: #1A2232;
}

.bg-info {
  background: radial-gradient(circle, rgb(26, 34, 50) 0%, rgb(0, 0, 0) 100%);
  -webkit-box-shadow: 0px 3px 10px 2px rgb(142, 255, 0);
  -moz-box-shadow: 0px 3px 10px 2px rgb(142, 255, 0);
  box-shadow: 0px 3px 10px 2px rgb(142, 255, 0);
}

.footer {
  background-color: #1A2232;
  padding: 2rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #fff;
}